  color: #00a9e2; }

span.nopower {
  font-weight: bold;
  color: #e22d00; }

div.machineStateText span {
  font-weight: bold; }

div.machineStateText span.off, div.machineStateText span.noitems {
  color: #ffd30b; }

div.machineStateText span.running {
  color: #0ce22e; }

div.machineStateText span.waiting {
  color: #00a9e2; }

