  width: 400px;
  background-color: #333; }
  div.scienceSelectionPanel div.scienceUnlocksContainer, div.scienceSelectionPanel div.unlocksItemsContainer, div.scienceSelectionPanel div.scienceRequiredContainer {
    margin-top: 10px; }
  div.scienceSelectionPanel div.unlocksItems {
